import { useSnackbar } from 'notistack'
import { useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'

import type { SelectChangeEvent } from '@mui/material'
import { Container } from '@mui/material'

import { DATE_MESSAGE } from 'src/data/messages'
import { FIRST_SEMESTER, FIRST_SEMESTER_NAME, SECOND_SEMESTER_NAME } from 'src/data/semester'

import type { PutAccountHistoryRequestData } from 'src/api/clubAccount'

import type { Semester } from 'src/models/date'
import { defaultDate } from 'src/models/date'
import { type DateProps, isSemester, isYear } from 'src/models/date'

import DateForm from 'src/components/DateForm'
import ErrorMessageBox from 'src/components/ErrorMessageBox'
import PageTitleWrapper from 'src/components/PageTitleWrapper'

import HistoryGrid from 'src/content/manage/Account/HistoryGrid'
import HistoryInput from 'src/content/manage/Account/HistoryInput'
import SemesterSummary from 'src/content/manage/Account/SemesterSummary'
import PageHeader from 'src/content/manage/PageHeader'

import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

const AccountPage: React.FC = () => {
  const [clickedFee, setClickedFee] = useState<PutAccountHistoryRequestData>()
  const [isActivateRefresh, setIsActivateRefresh] = useState(false)
  const [isExistAccount, setIsExistAccount] = useState(true)

  const { enqueueSnackbar } = useSnackbar()
  const { userInfo } = useSafeAuthUser()
  const params = useParams()
  const clubId = Number(params.id)
  const club = userInfo?.clubs.find(club => club.id === clubId)

  const clubDate: DateProps = { year: club?.year || defaultDate.year, semester: club?.semester || defaultDate.semester }
  const [date, setDate] = useState<DateProps>({
    year: clubDate.year,
    semester: clubDate.semester
  })

  const handleExistAccount = (isExists: boolean) => {
    setIsExistAccount(isExists)
  }
  const handleClickedFee: (e: PutAccountHistoryRequestData) => void = e => {
    setClickedFee(e)
  }

  const handleDateChange = ({ target: { value } }: React.ChangeEvent<HTMLInputElement> | SelectChangeEvent<string>) => {
    if (isSemester(value)) {
      setDate(prevDate => ({ ...prevDate, semester: value as Semester }))
      return
    }
    if (isYear(value)) {
      setDate(prevDate => ({ ...prevDate, year: Number(value) }))
      return
    }
    enqueueSnackbar(DATE_MESSAGE.INVALID_DATE, { variant: 'warning' })
  }

  const refreshInformation = () => {
    setIsActivateRefresh(prev => !prev)
  }
  return (
    <>
      <Helmet>
        <title>회비 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club?.name ?? ''} buttonText={'회원등급 관리'} link={`/manage/${clubId}/fee/edit`} />
      </PageTitleWrapper>
      <Container maxWidth={'lg'}>
        <DateForm clubDate={clubDate} date={date} handleDateChange={handleDateChange} />
        {!isExistAccount && (
          <ErrorMessageBox
            children={`${date.year}년도 ${
              date.semester === FIRST_SEMESTER ? FIRST_SEMESTER_NAME : SECOND_SEMESTER_NAME
            }에는 계좌가
          존재하지 않습니다.`}
          />
        )}
        <SemesterSummary isRefreshFlag={isActivateRefresh} date={date} handleExistAccount={handleExistAccount} />
        {isExistAccount && <HistoryInput clickedFee={clickedFee} refresh={refreshInformation} date={date} />}
        <HistoryGrid
          handleRowClick={handleClickedFee}
          isRefreshFlag={isActivateRefresh}
          refresh={refreshInformation}
          date={date}
        />
      </Container>
    </>
  )
}

export default AccountPage
