import { AxiosError } from 'axios'
import { useSnackbar } from 'notistack'
import type { FC } from 'react'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { useParams } from 'react-router'

import {
  Card,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'

import { COMMON_MESSAGE, RESPONSE_MESSAGE } from 'src/data/messages'

import type { GetAccountInfoResponseData } from 'src/api/clubAccount'
import { getAccountInfo } from 'src/api/clubAccount'

import { type DateProps } from 'src/models/date'

import { EXPENSE_COLOR, INCOME_COLOR } from 'src/content/manage/Account/accountPageColors'
import applyComma from 'src/content/manage/Account/applyComma'

interface CustomTableCell {
  cellValue: string
  cellColor?: string
}

interface SemsterSummaryProps {
  date: DateProps
  isRefreshFlag: boolean
  handleExistAccount: (isExists: boolean) => void
}

const SemesterSummary: FC<SemsterSummaryProps> = ({ date, isRefreshFlag, handleExistAccount }) => {
  const [summary, setSummary] = useState<GetAccountInfoResponseData>()

  const params = useParams()
  const theme = useTheme()
  const isUpperThanSm = useMediaQuery(theme.breakpoints.up('sm'))
  const { enqueueSnackbar } = useSnackbar()
  const authHeader = useAuthHeader()

  const isValidNumbers = !!summary?.totalIncome && !!summary?.totalExpense
  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await getAccountInfo({
          clubId: params.id as unknown as number,
          date: date,
          token: authHeader()
        })
        handleExistAccount(true)
        setSummary(res)
      } catch (err) {
        if (err instanceof AxiosError && err.response?.data?.message === RESPONSE_MESSAGE.ERROR_NO_INFO) {
          handleExistAccount(false)
          setSummary({ totalExpense: 0, totalIncome: 0 })
          return
        }
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
    fetchData()
  }, [isRefreshFlag, date])

  return (
    <Card style={{ marginBottom: 10 }}>
      <TableContainer>
        <Table>
          {isUpperThanSm && (
            <TableHead>
              <TableRow>
                <TableCell>총 수입</TableCell>
                <TableCell>총 지출</TableCell>
                <TableCell>사용가능금액</TableCell>
              </TableRow>
            </TableHead>
          )}
          <TableBody>
            <TableRow hover>
              <CustomTableCell cellValue={`+${applyComma(summary?.totalIncome)}`} cellColor={INCOME_COLOR} />
              <CustomTableCell cellValue={`-${applyComma(summary?.totalExpense)}`} cellColor={EXPENSE_COLOR} />
              <CustomTableCell
                cellValue={`${isValidNumbers ? applyComma(summary?.totalIncome - summary?.totalExpense) : '0'}`}
              />
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </Card>
  )
}

export default SemesterSummary

export const CustomTableCell: FC<CustomTableCell> = ({ cellValue, cellColor = 'text.primary' }) => {
  return (
    <TableCell width={'25%'}>
      <Typography variant={'body1'} fontWeight={'bold'} color={cellColor} gutterBottom noWrap>
        {cellValue}
      </Typography>
    </TableCell>
  )
}
